import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Api , Inputs, User} from '../../providers';
/**
 * Generated class for the FiltrePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-filtre',
  templateUrl: 'filtre.html',
})
export class FiltrePage {

  public data : any =[];

  constructor(public user : User, public inputs : Inputs, public viewCtrl : ViewController, public api : Api , public navCtrl: NavController, public navParams: NavParams) {
    this.loadParams();
  }

  ionViewDidLoad() {

 
  
  }


  loadParams(){
    if (this.navParams.data.page == 'map') {

      this.api.filtre = this.api.filtreMap;

    } else if (this.navParams.data.page == 'userProperties') {

      this.api.filtre = this.api.filtreUserProperties;

    } else if (this.navParams.data.page == 'developments') {

      this.api.filtre = this.api.filtreDevelopments; 

    } else if (this.navParams.data.page == 'properties') {

      this.api.filtre = this.api.filtreProperties;

    } else if (this.navParams.data.page == 'buyer') {

      this.api.filtre = this.api.filtreBuyer;

    }

  }

  updateTownship(id){}

  updateSuburb(id) {}

  close(){

    if (this.navParams.data.page == 'map') {

      this.api.filtreMap = this.api.filtre;

    } else if (this.navParams.data.page == 'userProperties') {

      this.api.filtreUserProperties = this.api.filtre;

    } else if (this.navParams.data.page == 'developments') {

      this.api.filtreDevelopments = this.api.filtre;

    } else if (this.navParams.data.page == 'properties') {

      this.api.filtreProperties =this.api.filtre  ;

    } else if (this.navParams.data.page == 'buyer') {
      this.api.filtreBuyer = this.api.filtre;
    }

    this.viewCtrl.dismiss();
    
  }

  reset(){
  
    this.api.filtre           = [];
    this.api.filtre.status = ''; //Tipo de propiedad
    this.api.filtre.officeid = ''; //Tipo de propiedad
    this.api.filtre.type      = ''; //Tipo de propiedad
    this.api.filtre.operation = ''; //Tipo de operación
    this.api.filtre.priceMin  = ''; //Precio desde
    this.api.filtre.priceMax  = ''; //Precio hasta
    this.api.filtre.moneda    = ''; //Tipo de moneda
    this.api.filtre.bedMin    = ''; //Habitaciones desde
    this.api.filtre.bedMax    = ''; //Habitaciones hasta
    this.api.filtre.m2Min     = ''; //Metros de construcción desde
    this.api.filtre.m2Max     = ''; //Metros de construcción hasta
    this.api.filtre.state     = ''; //Estado
    this.api.filtre.township  = ''; //Municipio
    this.api.filtre.suburb    = ''; //colonia
    this.api.filtre.parkMin   = ''; //Estacionamientos desde
    this.api.filtre.parkMax   = ''; //Estacionamientos hasta
    this.api.filtre.development = ''; //Estacionamientos hasta

  }

}



